import React from 'react';
import './main.scss';

export default () => {
  const componentName = localStorage.getItem('component') || 'Sidebar';
  const Component = React.lazy(() => import(`../../components/${componentName}/${componentName}`));
  return (
    <React.Suspense fallback={<div>Loading...</div>}>
      <Component />
    </React.Suspense>
  );
};
